import React from 'react'
import PropTypes from 'prop-types'
import Mailchimp from 'react-mailchimp-form'
import withStyles from './mailchimp-email-capture.style'
import Typography from '@material-ui/core/Typography'

const MailchimpEmailCapture = ({ action, classes, label }) => (
  <div className={classes.root}>
    <Typography variant="h6">{label}</Typography>
    <Mailchimp
      action={action}
      fields={[
        {
          name: 'NAME',
          placeholder: 'Name',
          type: 'text',
          required: true,
        },
        {
          name: 'ORT',
          placeholder: 'Ort',
          type: 'text',
          required: true,
        },
        {
          name: 'EMAIL',
          placeholder: 'Email',
          type: 'email',
          required: true,
        },
      ]}
      messages={{
        success:
          'Wir haben Ihre Emailadresse erhalten! Wir werden uns in Kürze bei Ihnen melden.',
        error:
          'Ein unerwarteter Fehler ist passiert.Seien Sie unbesorgt. Warum rufen Sie uns nicht einfach an?',
        empty: 'Bitte geben Sie eine funktionierende Emailadresse an',
        duplicate:
          'Sind Sie sich sicher, dass Sie das nicht schon mal gemacht haben?',
        button: 'Kontakt aufnehmen!',
      }}
    />
    <p>
      Ihre eingegebenen Daten werden an Mailchimp übertragen. Sie erhalten
      weitere Informationen zu dem Umgang Ihrer Daten in den{' '}
      <a href="https://mailchimp.com/legal/privacy/">
        Datenschutzbestimmungen von Mailchimp
      </a>
      .
    </p>
  </div>
)

MailchimpEmailCapture.propTypes = {
  action: PropTypes.string,
  classes: PropTypes.object,
  label: PropTypes.string,
}

MailchimpEmailCapture.defaultProps = {
  action:
    'https://compose.us7.list-manage.com/subscribe/post?u=515806199910ecc73065ec557&id=2f35862729',
  label: 'Label Text Not Set!',
}
export default withStyles(MailchimpEmailCapture)
