import React from 'react'
import Grid from '@material-ui/core/Grid'
import withStyles from './landing-page-hero.style'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import Container from '@material-ui/core/Container'

const LandingPageHero = ({ classes, heroImage, backgroundImage, usp, sub }) => (
  <div
    className={classes.root}
    style={{
      backgroundImage: `url(${backgroundImage})`,
      transform: 'translate3d(0px, 0px, 0px)',
    }}
  >
    <Container>
      <Grid container>
        <Grid item xs={12} md={6} className={classes.headline}>
          <Typography className={classes.usp}>{usp}</Typography>
          <Typography className={classes.sub}>{sub}</Typography>
        </Grid>
        <Grid item xs={12} md={6} className={classes.hero}>
          <img src={heroImage} alt={usp} />
        </Grid>
      </Grid>
    </Container>
  </div>
)

LandingPageHero.propsTypes = {
  classes: PropTypes.object.isRequired,
  heroImage: PropTypes.node.isRequired,
  backgroundImage: PropTypes.node.isRequired,
  usp: PropTypes.string,
  sub: PropTypes.string,
}

LandingPageHero.defaultProps = {
  usp: 'USP Text Not Set.',
  sub: 'SUB Text Not Set.',
}

export default withStyles(LandingPageHero)
