import React from 'react'
import PropTypes from 'prop-types'
import theme from '../../../lib/theme'
import ThemeProvider from '@material-ui/styles/ThemeProvider'
import CssBaseline from '@material-ui/core/CssBaseline'
import withStyles from './landing-page-layout.style'
import LandingPageHero from '../landing-page-hero/landing-page-hero'
import LandingPageFooter from '../landing-page-footer/landing-page-footer'
import Container from '@material-ui/core/Container'

const LandingPageLayout = ({
  children,
  classes,
  heroImage,
  backgroundImage,
  usp,
  sub,
}) => (
  <React.Fragment>
    <CssBaseline />
    <ThemeProvider theme={theme}>
      <LandingPageHero
        heroImage={heroImage}
        backgroundImage={backgroundImage}
        usp={usp}
        sub={sub}
      />
      <main className={classes.main}>
        <Container>{children}</Container>
      </main>
      <LandingPageFooter />
    </ThemeProvider>
  </React.Fragment>
)

LandingPageLayout.propsTypes = {
  children: PropTypes.node.isRequired,
  classes: PropTypes.object.isRequired,
  heroImage: PropTypes.node.isRequired,
  backgroundImage: PropTypes.node.isRequired,
  usp: PropTypes.string,
  sub: PropTypes.string,
}

LandingPageLayout.defaultProps = {
  usp: 'USP Text Not Set.',
  sub: 'SUB Text Not Set.',
}

export default withStyles(LandingPageLayout)
